.App {
  text-align: center;
  margin-bottom: 100px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  padding: 10px;
  color: white;
}

.card {
  margin-top: 10px;
  -webkit-box-shadow: 1px 1px 1px gray;
          box-shadow: 1px 1px 1px gray;

}

.imageWrapper {
  margin-top: 20px;
  margin-bottom: 20px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.employee-enter {
  opacity: 0.01;
}

.employee-enter.employee-enter-active {
  opacity: 1;
  -webkit-transition: 700ms;
  -o-transition: 700ms;
  transition: 700ms;
}

.employee-leave {
  opacity: 1;
}

.employee-leave.employee-leave-active {
  opacity: 0.01;
  -webkit-transition: 700ms;
  -o-transition: 700ms;
  transition: 700ms;
}


